import CustomHook from '/lib/vue-custom-hooks.js';
let MyPlugin = {};
MyPlugin.install = function(Vue) {
    CustomHook.init(Vue,{
         'Login':{
            name:'Login',
            watchKey: '$store.state.loginType',
            onUpdate(val,oldvue){
                //loginTypetrue
                return !!val;
            }
        },
         'UserInfo':{
            name:'UserInfo',
            watchKey: '$store.state.userinfo',
            //userinfodeep
            deep: true,
            onUpdate(val){
                //userinfoname
                return !!val.name;
            }
        }
    })
}
export default MyPlugin