<template>
  <div class="home" style="background: #FAB87F;">
    <h1>This is an home page</h1>
  </div>
</template>

<script>
export default {
	name: 'Home',
	created(){
		this.$store.commit('logs','created(home.vue)');
	},
	mounted(){
		this.$store.commit('logs','mounted(home.vue)');
	},
	onLoginCreated(){
		this.$store.commit('logs','onLoginCreated(home.vue)');
	},
	onUserInfoCreated(){
		this.$store.commit('logs','onUserInfoCreated(home.vue)');
	},
	onLoginUserInfoCreated(){
		this.$store.commit('logs','onLoginUserInfoCreated(home.vue)');
	},
	onLoginMounted(){
		this.$store.commit('logs','onLoginMounted(home.vue)');
	}
}
</script>
